.voucher-container {
  background-color: white;
  // padding: 0 150px 0 150px;
  padding-bottom: 30px;

  .title-header {
    padding-top: 30px;
  }

  .title-item {
    padding: 32px 0 16px 0;

    .see-all {
      display: flex;
      align-items: center;

      .see-all-txt {
        color: #c4a732;
        font-size: 14px;
        padding-right: 0.5em;
      }
    }

    .title {
      color: #000000;
      font-size: 36px;
      font-weight: bold;
    }

    .customButton {
      background: #c4a732;
      border-radius: 30px;
      width: 111px;
      color: white;
      font-size: 14px;
    }
  }
  .box-button {
    padding: 40px 0 0px 0;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
}
